import { StockItem } from '../model/StockItem'

export class StockReport {

    details: {
        id: number,
        name: string
        comment: string
    }
    items: StockItem[]= []

    constructor(id: number, name: string, comment: string, items: StockItem[]) {
        this.details = {id : id, name : name, comment : comment}
        this.items = items
    }
}