import { Component, OnInit } from '@angular/core';
import { StockReportService } from '../service/stock-report-service';
import { StockItem } from '../model/StockItem'
import { Product } from '../model/Product'
import { StockReport } from '../model/StockReport';

@Component({
  selector: 'stock-report',
  templateUrl: './stock-report.component.html',
  styleUrls: ['./stock-report.component.css']
})
export class StockReportComponent implements OnInit {

  reportId: number = 1
  report: StockReport

  constructor(private stockReportService: StockReportService) { }

  ngOnInit() {
    this.stockReportService.getStockReport(this.reportId).subscribe(data => {
      var items: StockItem[] = []
      for (let item of data.items) {
        let product = new Product(item.product.id, item.product.name, item.product.price)
        let stockItem = new StockItem(item.id, item.quantity, product)
        items.push(stockItem)
      }
      this.report = new StockReport(data.id, data.name, data.comment, items)
      console.log("1 : " + this.report)
    })
    console.log("2 : aaaa")
  }

  onNewStockItem($event) {
    let stockItem = this.stockReportService.addItemToReport(this.reportId, $event)
    this.report.items.push(stockItem)
  }
}
