import { StockItem } from '../model/StockItem'
import { Product } from '../model/Product'
import { Injectable, EventEmitter } from '@angular/core'
import { HttpService } from './http-service'
import { StockReport } from '../model/StockReport'
import { Observable } from "rxjs"

@Injectable()
export class StockReportService {

    constructor(private httpApi: HttpService){}

    getStockReport(id: number) : Observable<any> {
        console.log("Get report " + id)
        var stockReport: StockReport
        return this.httpApi.get("/v1/stockReport/get/" + id)
    }

    addItemToReport(idReport: number, item: StockItem) : StockItem {
        console.log("Add item[ " + ", " + item.quantity + " to report " + idReport)
        let stockItem
        var addStockItemRequest = {
            "stockReportId" : idReport,
            "stockItem" : item,
            "type" : item.type
        }
        this.httpApi.post("/v1/stock/update", addStockItemRequest).subscribe(
            data => {
                let product = new Product(data.item.product.id, data.item.product.name, data.item.product.price)
                stockItem = new StockItem(data.item.id, data.item.quantity, product)
            },
            error => {
                debugger;
                console.log(error)
            }
        )
        //this.stockItemChanged.emit(this.stockItems)
        return stockItem
    }

}